/**
 * @file 书柜初始化数据
 * 宽 & 高
 * @author Yangholmes 2025-03-30
 */

import { defineComponent, PropType, toRefs } from 'vue';
import { useI18n } from 'vue-i18n';

import { NForm, NFormItem, NInputNumber } from 'naive-ui';

import { defaultSize } from '../constants';

export interface CellSize {
  width: number;
  height: number;
}

export default defineComponent({
  name: 'InitForm',
  props: {
    size: {
      type: Object as PropType<CellSize>,
      default: { width: defaultSize, height: defaultSize }
    },
    onChange: {
      type: Function as PropType<(size: CellSize) => void>,
    }
  },
  setup(props) {
    const { size } = toRefs(props);
    const { t } = useI18n({ useScope: 'global' });

    props.onChange?.(size.value);

    function onChangeValue(value: number | null, key: keyof CellSize) {
      if (value === null) {
        return;
      }
      size.value[key] = value;
      props.onChange?.(size.value);
    }

    return () => (<NForm
      style={{ marginTop: '24px' }}
      inline
      labelPlacement="left"
      model={size.value}
    >
      <NFormItem label={t('common.width')} path="width">
        <NInputNumber
          placeholder={t('common.width')}
          buttonPlacement="both"
          min={1} max={20}
          value={size.value?.width}
          onUpdateValue={(value) => onChangeValue(value, 'width')}
        />
      </NFormItem>
      <NFormItem label={t('common.height')} path="height">
        <NInputNumber
          placeholder={t('common.height')}
          buttonPlacement="both"
          min={1} max={20}
          value={size.value?.height}
          onUpdateValue={(value) => onChangeValue(value, 'height')}
        />
      </NFormItem>
    </NForm>);
  }
});

